import {
  CloudSyncOutlined,
  MehOutlined,
  RedoOutlined,
  SmileOutlined,
} from '@ant-design/icons';
import SentimentVeryDissatisfiedIcon from '@material-ui/icons/SentimentVeryDissatisfied';
import {
  Button,
  Checkbox,
  Select,
  Space,
  Switch,
  Table,
  TimePicker,
} from 'antd';
import { ColumnsType } from 'antd/lib/table';
import Text from 'antd/lib/typography/Text';
import axios from 'axios';
import moment from 'moment';
import React, { useEffect, useState } from 'react';
import { confirm } from '../ConfirmModal';
import MenuGuide from '../MenuGuide';
import './TableColumns.css';

const PowerTable = ({
  dataSource,
  getEquipIndex,
}: {
  dataSource: any[];
  getEquipIndex: VoidFunction;
}) => {
  const [allPowerTime, setAllPowerTime] = useState({
    eqpPowerStartTime: '00:00',
    eqpPowerEndTime: '00:00',
  });
  const [isLoading, setIsLoading] = useState(true);
  const [currentPage, setCurrentPage] = useState(
    Number.parseInt(sessionStorage.getItem('currentPage') || '1')
  );

  const columns: ColumnsType<any> = [
    { title: '시스템명', dataIndex: 'eqpName', align: 'center' },
    {
      title: '시스템 전원 예약 설정',
      children: [
        {
          title: '사용 여부',
          dataIndex: 'eqpIsPowerBatch',
          width: 100,
          align: 'center',
          render: (item: any, record: { idx: number }, idx: any) => {
            return (
              <Select
                style={{ width: '90px' }}
                value={item}
                onChange={(e) => {
                  console.log(e);
                  axios
                    .post(`/equip/modifyIsPowerModule`, {
                      idx: record.idx,
                      eqpIsPowerBatch: e,
                    })
                    .then((res) => {
                      console.log(res);
                      getEquipIndex();
                    })
                    .catch((err) => {
                      console.error(err);
                    });
                }}
              >
                <Select.Option value={'Y'}>사용</Select.Option>
                <Select.Option value={'N'}>미사용</Select.Option>
              </Select>
            );
          },
        },
        {
          title: '시작 시간',
          dataIndex: 'eqpCPU',
          width: 100,
          align: 'center',
          render: (
            item: any,
            record: {
              [x: string]: moment.MomentInput;
              eqpIsPowerBatch: string;
              idx: number;
            },
            idx: number
          ) => {
            return (
              <TimePicker
                allowClear={false}
                format={'HH:mm'}
                placeholder='시작'
                disabled={record.eqpIsPowerBatch === 'N'}
                value={
                  record['eqpPowerStartTime']
                    ? moment(record['eqpPowerStartTime'], 'HH:mm')
                    : moment('00:00', 'HH:mm')
                }
                onChange={(e) => {
                  console.log(e);
                  if (e) {
                    axios
                      .post(`/equip/modifyEqpPowerTimeForIdx`, {
                        eqpPowerStartTime: e.format('HH:mm'),
                        idx: record.idx,
                      })
                      .then((res) => {
                        console.log(res);
                        getEquipIndex();
                      })
                      .catch((err) => {
                        console.error(err);
                      });
                  }
                }}
              />
            );
          },
        },
        {
          title: '종료 시간',
          dataIndex: 'eqpCPU',
          width: 100,
          align: 'center',
          render: (
            item: any,
            record: {
              [x: string]: moment.MomentInput;
              eqpIsPowerBatch: string;
              idx: number;
            },
            idx: number
          ) => {
            return (
              <TimePicker
                allowClear={false}
                format={'HH:mm'}
                placeholder='종료'
                disabled={record.eqpIsPowerBatch === 'N'}
                value={
                  record['eqpPowerStartTime']
                    ? moment(record['eqpPowerEndTime'], 'HH:mm')
                    : moment('00:00', 'HH:mm')
                }
                onChange={(e) => {
                  console.log(e);
                  if (e)
                    axios
                      .post(`/equip/modifyEqpPowerTimeForIdx`, {
                        eqpPowerEndTime: e.format('HH:mm'),
                        idx: record.idx,
                      })
                      .then((res) => {
                        console.log(res);
                        getEquipIndex();
                      })
                      .catch((err) => {
                        console.error(err);
                      });
                }}
              />
            );
          },
        },
      ],
    },
    {
      title: '시스템관리',
      children: [
        {
          title: '전원',
          dataIndex: 'power',
          width: 50,
          align: 'center',
          render: (
            text: any,
            record: { idx: number; powerYN: string },
            index: number
          ) => {
            const togglePowerSwich = () => {
              setIsLoading(true);
              // if(record.eqpOS == 'WINDOWS' && record.powerYN == 'N') {
              //   //   OS Windows이고 powerOff 상태일 때
              //   // 동작하지 않도록 한다.
              //   // 윈도우는 원격으로 켤수없기 때문이다.
              //   setIsLoading(false);
              //   return;
              // }
              axios
                .post('/power/control', {
                  idx: record.idx,
                  status: record.powerYN === 'Y' ? 0 : 1,
                })
                .then((res) => {
                  setTimeout(() => {
                    setIsLoading(false);
                    getEquipIndex();
                  }, 10000);
                })
                .catch((err) => {
                  setIsLoading(false);

                  console.error(err);
                })
                .finally(() => {});
            };
            return (
              <Switch
                checked={dataSource[index].powerYN === 'Y'}
                onClick={togglePowerSwich}
              ></Switch>
            );
          },
        },
        {
          title: '리셋',
          dataIndex: 'reset',
          key: '55',
          width: 50,
          align: 'center',
          render: (
            text: any,
            record: { eqpOS: string; idx: number; pwrMgtAddress: any },
            index: any
          ) => {
            const syncEquipment = (e: { preventDefault: () => void }) => {
              if (record.eqpOS === 'WINDOWS') {
                setIsLoading(true);
                e.preventDefault();
                axios
                  .get('/windows/reset', {
                    params: {
                      idx: record.idx,
                      eqpOS: record.eqpOS,
                      pwrMgtAddress: record.pwrMgtAddress,
                    },
                  })
                  .then((res) => {
                    getEquipIndex();
                    setIsLoading(false);
                    // setTimeout(() => {
                    //   setIsLoading(false);
                    // }, 10000);
                  })
                  .catch((err) => {
                    setIsLoading(false);
                    console.error(err);
                  });
              } else {
                confirm({
                  title: `OS가 WINDOWS 인 경우만 동작합니다.`,
                  content: ``,
                  onOk: () => {},
                });
              }
            };
            return (
              <span
                style={{ fontSize: '1.5em', cursor: 'pointer' }}
                onClick={syncEquipment}
              >
                <RedoOutlined style={{ cursor: 'pointer' }} />
              </span>
            );
          },
        },
        {
          title: '동기화',
          dataIndex: 'sync',
          key: '55',
          width: 80,
          align: 'center',
          render: (
            text: any,
            record: { idx: number; eqpOS: any; pwrMgtAddress: any },
            index: any
          ) => {
            const syncEquipment = (e: { preventDefault: () => void }) => {
              setIsLoading(true);
              e.preventDefault();
              axios
                .get('/power/sync', {
                  params: {
                    idx: record.idx,
                    eqpOS: record.eqpOS,
                    pwrMgtAddress: record.pwrMgtAddress,
                  },
                })
                .then((res) => {
                  getEquipIndex();
                  setTimeout(() => {
                    setIsLoading(false);
                  }, 10000);
                })
                .catch((err) => {
                  setIsLoading(false);
                  console.error(err);
                });
            };
            return (
              <span
                style={{ fontSize: '1.5em', cursor: 'pointer' }}
                onClick={syncEquipment}
              >
                <CloudSyncOutlined style={{ cursor: 'pointer' }} />
              </span>
            );
          },
        },
      ],
    },
    {
      title: '운영관리',
      children: [
        {
          title: 'MEM',
          dataIndex: 'memUsage',
          key: '66',
          width: 100,
          align: 'center',
        },
        {
          title: 'CPU',
          dataIndex: 'cpuUsage',
          key: '77',
          width: 100,
          align: 'center',
        },
        {
          title: '현황',
          dataIndex: 'status',
          key: '88',
          width: 100,
          align: 'center',
          render: (
            text: any,
            record: { powerYN: string; memNumber: number; cpuNumber: number },
            index: any
          ) => {
            if (
              record.powerYN === 'Y' &&
              record.memNumber < 50 &&
              record.cpuNumber < 50
            ) {
              return (
                <SmileOutlined
                  style={{ fontSize: '1.5em', color: '#52c41a' }}
                />
              );
            } else if (
              record.powerYN === 'Y' &&
              record.memNumber < 80 &&
              record.cpuNumber < 80
            ) {
              return (
                <MehOutlined style={{ fontSize: '1.5em', color: '#fcba03' }} />
              );
            } else if (
              record.powerYN === 'Y' &&
              record.memNumber < 100 &&
              record.cpuNumber < 100
            ) {
              return (
                <SentimentVeryDissatisfiedIcon
                  style={{ fontSize: '1.5em', color: '#ff141d' }}
                />
              );
            } else {
              return (
                <SmileOutlined
                  style={{ fontSize: '1.5em', color: '#555555' }}
                />
              );
            }
          },
        },
      ],
    },
  ];

  useEffect(() => {
    setTimeout(() => {
      setIsLoading(false);
    }, 500);
  }, []);

  const onAllOnOff = (id: string) => {
    setIsLoading(true);
    if (id !== 'allOn') {
      confirm({
        title: `모든 장비 전원을 끄시겠습니까?`,
        content: ``,
        onOk: () => {
          axios
            .get('/power/all', {
              params: {
                mode: id === 'allOn' ? '1' : '0',
              },
            })
            .then((res) => {
              // setDataSource(res.data.dataSource);
              getEquipIndex();
              setIsLoading(false);
            })
            .catch((err) => {
              setIsLoading(false);
              console.error(err);
            });
        },
        onCancle: () => {
          setIsLoading(false);
        },
      });
    } else {
      axios
        .get('/power/all', {
          params: {
            mode: id === 'allOn' ? '1' : '0',
          },
        })
        .then((res) => {
          // setDataSource(res.data.dataSource);
          getEquipIndex();
          setTimeout(() => {
            setIsLoading(false);
          }, 5000);
        })
        .catch((err) => {
          setIsLoading(false);
          console.error(err);
        });
    }
  };

  const onAllSync = (e: any) => {
    setIsLoading(true);
    axios
      .get('/power/syncAll', {})
      .then((res) => {
        // setDataSource(res.data.dataSource);
        getEquipIndex();
        setTimeout(() => {
          setIsLoading(false);
        }, 5000);
      })
      .catch((err) => {
        setIsLoading(false);
        console.error(err);
      });
  };

  return (
    <>
      <MenuGuide
        title={'장비 관리 ▶ 전원 관리'}
        subTitle={'스마트 전원[ON/OFF] 관리가 가능합니다.'}
      />
      <Space className='TotalController' direction='vertical' size='middle'>
        <Space size='large'>
          <div
            style={{
              display: 'flex',
              width: '100%',
              justifyContent: 'flex-start',
            }}
          >
            <div style={{ width: '123px' }}>
              <Text strong>전체 요일 설정 : </Text>
            </div>
            <Checkbox.Group
              style={{ width: '100%' }}
              onChange={(e) => {
                axios
                  .post(`/power/setWeekDay`, {
                    eqpPowerWeekday:
                      e.length === 0
                        ? '0,1,2,3,4,5,6'
                        : e
                            .sort(
                              (a, b) =>
                                parseInt(a.toString()) - parseInt(b.toString())
                            )
                            .toString(),
                  })
                  .then((res) => {
                    getEquipIndex();
                  })
                  .catch((err) => {
                    console.error(err);
                  });
              }}
              value={dataSource[0]?.eqpPowerWeekday?.split(',')}
            >
              <Checkbox value='0'>일요일</Checkbox>
              <Checkbox value='1'>월요일</Checkbox>
              <Checkbox value='2'>화요일</Checkbox>
              <Checkbox value='3'>수요일</Checkbox>
              <Checkbox value='4'>목요일</Checkbox>
              <Checkbox value='5'>금요일</Checkbox>
              <Checkbox value='6'>토요일</Checkbox>
            </Checkbox.Group>
          </div>
        </Space>
        <Space size='large'>
          <div>
            <Text strong>전체 시작 시간 : </Text>
            <TimePicker
              placeholder='시작'
              format={'HH:mm'}
              value={moment(allPowerTime.eqpPowerStartTime, 'HH:mm')}
              onChange={(e) => {
                if (e)
                  setAllPowerTime({
                    ...allPowerTime,
                    eqpPowerStartTime: e.format('HH:mm'),
                  });
              }}
            />
          </div>
          <div>
            <Text strong>전체 종료 시간 : </Text>
            <TimePicker
              placeholder='종료'
              format={'HH:mm'}
              value={moment(allPowerTime.eqpPowerEndTime, 'HH:mm')}
              onChange={(e) => {
                if (e)
                  setAllPowerTime({
                    ...allPowerTime,
                    eqpPowerEndTime: e.format('HH:mm'),
                  });
              }}
            />
          </div>
          <div>
            <Button
              onClick={() => {
                confirm({
                  title: `전체 장비의 시간을 조정합니다.`,
                  content: `설정하시겠습니까?`,
                  onOk: () => {
                    axios
                      .post(`/equip/modifyPowerTimeForAll`, {
                        eqpPowerStartTime: allPowerTime.eqpPowerStartTime,
                        eqpPowerEndTime: allPowerTime.eqpPowerEndTime,
                      })
                      .then((res) => {
                        console.log(res);
                        getEquipIndex();
                      })
                      .catch((err) => {
                        console.error(err);
                      });
                  },
                });
              }}
            >
              적용
            </Button>
          </div>
        </Space>
        <div>
          <Text strong>전체 사용 여부 : </Text>
          <Select
            style={{ width: '132px', textAlign: 'center' }}
            defaultValue={'Y'}
            onChange={(e) => {
              console.log(e);
              confirm({
                title: `전체 장비의 예약설정을 조정합니다.`,
                content: `${
                  e === 'Y' ? '사용' : '미사용'
                }으로 설정하시겠습니까?`,
                onOk: () => {
                  axios
                    .post(`/equip/modifyIsPowerModule`, {
                      eqpIsPowerBatch: e,
                    })
                    .then((res) => {
                      console.log(res);
                      getEquipIndex();
                    })
                    .catch((err) => {
                      console.error(err);
                    });
                },
              });
            }}
          >
            <Select.Option value={'Y'}>사용</Select.Option>
            <Select.Option value={'N'}>미사용</Select.Option>
          </Select>
        </div>
        <div>
          <Text strong>전체 전원 제어 : </Text>
          <Button
            onClick={() => {
              onAllOnOff('allOn');
            }}
            id='allOn'
            disabled={isLoading}
          >
            전체 전원 ON
          </Button>
          <Button
            onClick={() => {
              onAllOnOff('allOff');
            }}
            id='allOff'
            style={{ marginLeft: '20px' }}
            disabled={isLoading}
          >
            전체 전원 OFF
          </Button>
          <Button
            onClick={onAllSync}
            style={{ marginLeft: '20px' }}
            disabled={isLoading}
          >
            <CloudSyncOutlined />
            전체 동기화
          </Button>
        </div>
      </Space>
      <Table
        loading={isLoading}
        rowKey={(dataSource) => dataSource.idx}
        dataSource={dataSource}
        columns={columns}
        size='small'
        bordered
        pagination={{
          pageSize: 20,
          current: currentPage,
          onChange: (e) => {
            if (e) {
              sessionStorage.setItem('currentPage', e.toString());
              setCurrentPage(e);
            }
          },
        }}
      ></Table>
    </>
  );
};

export default PowerTable;
