import Axios from 'axios';
import { CaptionAddDetailType, CaptionDetailType } from '../types/Caption';
import apiStrings from './api.server.json';

const API = apiStrings['api-uri'].caption;
export const requestPutCaption = async (data: Partial<CaptionDetailType>) => {
  try {
    const response = await Axios.put(`${API.caption}/${data.caption_idx}`, data);
    return response;
  } catch (err) {
    console.error(err);
  }
};
export const requestCreateCaption = async (data: Partial<CaptionAddDetailType>) => {
  try {
    const response = await Axios.post(`${API.caption}`, data);
    return response;
  } catch (err) {
    console.error(err);
  }
};
export const requestGetCaption = async (caption_idx: number) => {
  try {
    const response = await Axios.get(`${API.caption}/${caption_idx}`);
    return response;
  } catch (err) {
    console.error(err);
  }
};

export const requestGetCaptionList = async () => {
  try {
    const response = await Axios.get(`${API.caption}`);
    return response;
  } catch (err) {
    console.error(err);
  }
};

export const requestDeleteCaption = async (caption_idx: number) => {
  try {
    const response = await Axios.delete(`${API.caption}/${caption_idx}`);
    return response;
  } catch (err) {
    console.error(err);
  }
};
