import InsertChartIcon from '@material-ui/icons/InsertChart';
import MenuBookIcon from '@material-ui/icons/MenuBook';
import PhonelinkIcon from '@material-ui/icons/Phonelink';
import TabletMacIcon from '@material-ui/icons/TabletMac';
import TvIcon from '@material-ui/icons/Tv';
import { Divider } from 'antd';
import classNames from 'classnames';
import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import { kBodyWidth } from './Constants';

const Menu = ({ children, active }: { children: React.ReactNode; active?: boolean }): JSX.Element => {
  return (
    <div
      className={classNames(
        'font-bmjua text-lg w-40',
        'flex justify-center items-center hover:text-yellow-500',
        `${active ? 'text-yellow-500' : 'text-white'}`
      )}
    >
      {children}
    </div>
  );
};

const Navbar = () => {
  const location = useLocation();

  const isActive = (_pathname: string) => location.pathname.includes(_pathname);

  return (
    <div className={classNames('w-full bg-transparent flex items-center')}>
      <div>
        <Link
          to={{
            pathname: '/',
          }}
          style={{ color: 'white', fontSize: '2rem', fontStyle: 'italic' }}
        >
          BMS
        </Link>
      </div>
      <Link to="/equip">
        <Menu active={isActive('/equip')}>
          <PhonelinkIcon />
          <div className={'ml-2'}>장비관리</div>
        </Menu>
      </Link>
      <Divider type="vertical" className={classNames('bg-white w-px h-5')} />
      {/* <Link to='/is'>
        <Menu active={isActive('/is')}>
          <TabletMacIcon />
          <div className={'ml-2'}>예약관리</div>
        </Menu>
      </Link>
      <Divider type='vertical' className={classNames('bg-white w-px h-5')} /> */}
      <Link to="/screen">
        <Menu active={isActive('/screen')}>
          <TvIcon />
          <div className={'ml-2'}>화면관리</div>
        </Menu>
      </Link>
      {/* <Divider type='vertical' className={classNames('bg-white w-px h-5')} />
      <Link to='/book'>
        <Menu active={isActive('/book')}>
          <MenuBookIcon />
          <div className={'ml-2'}>사서추천도서</div>
        </Menu>
      </Link>
      <Divider type='vertical' className={classNames('bg-white w-px h-5')} /> */}
      {/* <Link to='/statistic'>
        <Menu active={isActive('/statistic')}>
          <InsertChartIcon />
          <div className={'ml-2'}>통계관리</div>
        </Menu>
      </Link> */}
    </div>
  );
};

export default Navbar;
