import {
  CaptionAddDetailType,
  CaptionDescType,
  CaptionDetailType,
  CaptionEquipidxType,
  CaptionIdxType,
  CaptionModifyContentsType,
  CaptionTitleType,
  CaptionType,
} from './../types/Caption';
import { createAsyncThunk, createSlice, PayloadAction } from '@reduxjs/toolkit';
import { message } from 'antd';
import { requestCreateCaption, requestDeleteCaption, requestGetCaption, requestGetCaptionList, requestPutCaption } from '../api';
import { CaptionsListType, ResponseBodyCaption, ResponseBodyCaptionsList } from '../types/Caption';
import { ResponseVo } from '../types/Common';

export const CAPTION = 'CAPTION' as const;

type SliceState = {
  /**
   * 디테일관련된 상태값들
   */
  title: string;
  desc: string;
  equip_idx: Array<number>;
  contents: Array<{
    content: string;
    caption_contents_idx: number;
    caption_idx: number;
    reg_date: string;
    seq: number;
    uuid?: string;
  }>;
  //Partial<Array<CaptionType>>
  /**
   * 자막 객체 리스트를 보관하기 위한 상태값
   */
  captionsList: CaptionsListType;

  //
  isSpin: boolean;
  isProgress: boolean;
  //
  caption_idx: number;
};

type PayloadType = {
  [CAPTION]: SliceState;
};

/**
 * 자막객체의 디테일을 얻어옴.
 */
export const fetchGetCaptionList = createAsyncThunk(`${CAPTION}/fetchGetCaptionList`, async () => {
  const response = await requestGetCaptionList();
  if (!!!response) return undefined;
  return response.data;
});

export const fetchCreateCaption = createAsyncThunk(`${CAPTION}/fetchCreateCaption`, async (data: Partial<CaptionAddDetailType>) => {
  const response = await requestCreateCaption({ ...data });
  if (!!!response) return undefined;
  return response.data;
});

export const fetchGetCaption = createAsyncThunk(`${CAPTION}/fetch`, async (caption_idx: number) => {
  const response = await requestGetCaption(caption_idx);
  if (!!!response) return undefined;
  return response.data;
});
/**
 * 자막 객체를 삭제함.
 */
export const fetchDeleteCaption = createAsyncThunk(`${CAPTION}/fetchGetEquipList`, async (caption_idx: number) => {
  const response = await requestDeleteCaption(caption_idx);
  if (!!!response) return undefined;
  return response.data;
});

/**
 * 자막 객체를 조회함.
 */
export const fetchGetEquipList = createAsyncThunk(`${CAPTION}/fetchGetEquipList`, async () => {});
/**
 * 자막 객체를 수정함.
 */
export const fetchPutCaption = createAsyncThunk(`${CAPTION}/fetchPutCaption`, async (data: Partial<CaptionDetailType>) => {
  const response = await requestPutCaption({ ...data });
  if (!!!response) return undefined;
  return response.data;
});

/**
 * 자막 객체를 생성함.
 */
export const fetchPostCaption = createAsyncThunk(`${CAPTION}/fetchPo stCaption`, async () => {});

const captionSlice = createSlice({
  name: CAPTION,
  initialState: {
    title: '자막',
    desc: '자막 설명',
    equip_idx: [],
    contents: [],
    captionsList: [],
    isSpin: false,
    isProgress: false,
    caption_idx: 0,
  } as SliceState,
  reducers: {
    setCaptionDesc: (state, action: PayloadAction<CaptionDescType>) => {
      state.desc = action.payload.desc;
    },
    setCaptionTitle: (state, action: PayloadAction<CaptionTitleType>) => {
      state.title = action.payload.title;
    },
    setCaptionEquipIdx: (state, action: PayloadAction<CaptionEquipidxType>) => {
      state.equip_idx = action.payload.equip_idx;
    },
    setCaptionContents: (state, action: PayloadAction<any>) => {
      state.contents = action.payload;
    },
    setCaptionSelectedIdx: (state, action: PayloadAction<CaptionIdxType>) => {
      state.caption_idx = action.payload.caption_idx;
    },
  },
  extraReducers: {
    // 자막을 수정하고, 추가하고, 삭제한다.
    // 자막객체의 인덱스로 조회한다. 이 때 조회하는 정보는 자막객체의 제목,설명,적용된장치들,자막목록이다. (디테일)
    // 자막 객체가 어떠한것이 있는지 조회하는 api가 필요하다. (리스트 조회)
    //

    [fetchGetCaptionList.pending.type]: (state) => {
      state.isProgress = true;
      state.isSpin = true;
    },
    [fetchGetCaptionList.fulfilled.type]: (state, action: PayloadAction<ResponseVo<ResponseBodyCaptionsList>>) => {
      if (action.payload === undefined) {
        message.error('실패하였습니다');
        return;
      }
      if (action.payload?.description.toLowerCase().includes('success')) {
        const result = action.payload.response_body;
        state.captionsList = result;
      } else {
        message.error('실패하였습니다');
      }
      state.isProgress = false;
      state.isSpin = false;
    },
    [fetchGetCaptionList.rejected.type]: (state, action: PayloadAction<ResponseVo<ResponseBodyCaptionsList>>) => {
      message.error('실패하였습니다');
      state.isProgress = false;
      state.isSpin = false;
    },

    [fetchPutCaption.pending.type]: (state) => {
      state.isProgress = true;
      state.isSpin = true;
    },
    [fetchPutCaption.fulfilled.type]: (state, action: PayloadAction<ResponseVo<ResponseBodyCaption>>) => {
      if (action.payload === undefined) {
        message.error('실패하였습니다');
        return;
      }
      if (action.payload?.description.toLowerCase().includes('success')) {
        const result = action.payload.response_body;
        state.contents = result;
        message.success('변경되었습니다.');
      } else {
        message.error('실패하였습니다');
      }
      state.isProgress = false;
      state.isSpin = false;
    },
    [fetchPutCaption.rejected.type]: (state, action: PayloadAction<ResponseVo<ResponseBodyCaptionsList>>) => {
      message.error('실패하였습니다');
      state.isProgress = false;
      state.isSpin = false;
    },
    [fetchDeleteCaption.pending.type]: (state) => {
      state.isProgress = true;
      state.isSpin = true;
    },
    [fetchDeleteCaption.fulfilled.type]: (state, action: PayloadAction<ResponseVo<ResponseBodyCaptionsList>>) => {
      if (action.payload === undefined) {
        message.error('실패하였습니다');
        return;
      }
      if (action.payload?.description.toLowerCase().includes('success')) {
        message.success('성공적으로 삭제되었습니다.');
      } else {
        message.error('실패하였습니다');
      }
      state.isProgress = false;
      state.isSpin = false;
    },
    [fetchDeleteCaption.rejected.type]: (state, action: PayloadAction<ResponseVo<ResponseBodyCaptionsList>>) => {
      message.error('실패하였습니다');
      state.isProgress = false;
      state.isSpin = false;
    },

    // 자막의 디테일정보를 받아온다.
    [fetchGetCaption.pending.type]: (state) => {
      state.isProgress = true;
      state.isSpin = true;
    },
    [fetchGetCaption.fulfilled.type]: (state, action: PayloadAction<ResponseVo<ResponseBodyCaption>>) => {
      if (action.payload === undefined) {
        message.error('실패하였습니다');
        return;
      }
      if (action.payload?.description.toLowerCase().includes('success')) {
        const result = action.payload.response_body;
        state.contents = result;
        console.log(result);
      } else {
        message.error('실패하였습니다');
      }
      state.isProgress = false;
      state.isSpin = false;
    },
    [fetchGetCaption.rejected.type]: (state, action: PayloadAction<ResponseVo<ResponseBodyCaptionsList>>) => {
      message.error('실패하였습니다');
      state.isProgress = false;
      state.isSpin = false;
    },
    [fetchCreateCaption.pending.type]: (state) => {
      state.isProgress = true;
      state.isSpin = true;
    },
    [fetchCreateCaption.fulfilled.type]: (state, action: PayloadAction<ResponseVo<ResponseBodyCaption>>) => {
      if (action.payload === undefined) {
        message.error('실패하였습니다');
        return;
      }
      if (action.payload?.description.toLowerCase().includes('success')) {
        const result = action.payload.response_body;
        state.contents = result;
        message.success('성공하였습니다');
      } else {
        message.error('실패하였습니다');
      }
      state.isProgress = false;
      state.isSpin = false;
    },
    [fetchCreateCaption.rejected.type]: (state, action: PayloadAction<ResponseVo<ResponseBodyCaptionsList>>) => {
      message.error('실패하였습니다');
      state.isProgress = false;
      state.isSpin = false;
    },
  },
});

export const { setCaptionDesc, setCaptionTitle, setCaptionEquipIdx, setCaptionContents, setCaptionSelectedIdx } = captionSlice.actions;
export default captionSlice.reducer;
export const selectCaption = (state: PayloadType) => state;
