import axios from 'axios';

// index.tsx에서 호출
export const setAxiosDefaultUrl = () => {
  process.env.NODE_ENV === 'development' ? (axios.defaults.baseURL = SERVER_URL[SITE.BULMU].DEV) : (axios.defaults.baseURL = SERVER_URL[SITE.BULMU].PROD);
};

export type ServerUrl = {
  PROD: string;
  DEV: string;
};

