import { Tabs } from 'antd';
import React from 'react';
import { useHistory, useRouteMatch } from 'react-router';
import { MatchParams } from '../types/Common';
import Banner from './banner/Banner';
import Recommand from './banner/Recommand';
import Info from './banner/Info';
// import ManagementCaption from './caption/ManagementCaption';
// import ManagementDcs from './dcs/ManagementDcs';
// import LgsTableContainer from './lgs/LgsTableContainer';
// import ManagementLss from './lss/ManagementLss';
// import ManagementPoll from './poll/ManagementPoll';

const ManagementScreen = () => {
  const { TabPane } = Tabs;
  const history = useHistory();
  const match = useRouteMatch<MatchParams>();

  function callback(key: string) {
    sessionStorage.setItem('currentPage', '1');
    history.push(`/screen/${key}`);
  }

  return (
    <Tabs onChange={callback} destroyInactiveTabPane type="card" activeKey={match.params.key}>
      {/* <TabPane tab="DCS" key="dcs">
        <ManagementDcs />
      </TabPane> */}
      <TabPane tab="행사안내(배너)" key="banner">
        <Banner />
      </TabPane>
      <TabPane tab="사서 추천도서" key="recommand">
        <Recommand />
      </TabPane>
      <TabPane tab="이용안내" key="info">
        <Info />
      </TabPane>
      {/* <TabPane tab='LSS' key='lss'>
        <ManagementLss />
      </TabPane>
      <TabPane tab='설문 관리' key='poll'>
        <ManagementPoll />
      </TabPane>
      <TabPane tab='LGS' key='lgs'>
        <LgsTableContainer />
      </TabPane>
      <TabPane tab='자막 관리' key='caption'>
        <ManagementCaption />
      </TabPane> */}
    </Tabs>
  );
};

export default ManagementScreen;
