import { LockOutlined, UserOutlined } from '@ant-design/icons';
import Avatar from '@material-ui/core/Avatar';
import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import { Input } from 'antd';
import axios from 'axios';
import classNames from 'classnames';
import React, { useContext, useEffect, useState } from 'react';
import { AuthContext } from '../context/AuthContext';
import logoImg from '../images/cms_as_logo.png';
import { noImg } from '../main/CommonImage';
import backgroundVideo from '../video/login_background_video.mp4';
import './SignInSide.css';

function Copyright() {
  useEffect(() => {
    document.body.className = 'cms-login';
  }, []);

  return (
    <Typography variant="body2" color="textSecondary" align="center" style={{ zIndex: 2 }}>
      {/* {'Copyright © '} */}
      <span style={{ color: 'white', margin: '0px 5px' }}>Basic Management System |</span>
      <span style={{ color: 'white' }}>Copyright © </span>
      <a color="inherit" href="http://greeneffect.co.kr/">
        GreenEffect
      </a>{' '}
      <span style={{ color: 'white' }}>{new Date().getFullYear()}</span>
      {'.'}
    </Typography>
  );
}

const useStyles = makeStyles((theme) => ({
  root: {
    height: '100vh',
  },
  image: {
    backgroundImage: 'url(https://source.unsplash.com/random)',
    backgroundRepeat: 'no-repeat',
    backgroundColor: theme.palette.grey[50],
    backgroundSize: 'cover',
    backgroundPosition: 'center',
  },
  paper: {
    margin: theme.spacing(8, 4),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    backgroundColor: 'white',
    width: '80px',
    height: '80px',
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

export default function SignInSide(props: any) {
  const userInfo = {
    id: 'admin',
    pw: '1234',
  };

  const classes = useStyles();

  const [isAuth, setIsAuth] = useContext(AuthContext);

  const [id, setId] = useState('');
  const [pw, setPw] = useState('');

  const onChangeId = (e: any) => {
    setId(e.target.value);
  };

  const onChangePw = (e: any) => {
    setPw(e.target.value);
  };

  const handleSubmit = (e: any) => {
    e.preventDefault();

    axios
      .get(`/user/login`, {
        params: {
          adminID: id.trim(),
          password: pw.trim(),
        },
      })
      .then((res) => {
        if ((res.data as any).login === 'Y') {
          setIsAuth(true);
          sessionStorage.setItem('userInfo', id);
          props.history.push('/');
        } else {
          alert('ID와 비밀번호를 정확히 입력해주세요');
        }
      })
      .catch((err) => {
        console.error(err);
      });
  };

  return (
    <Grid container component="main" className={classes.root}>
      {/* <CssBaseline /> */}
      {/* <Grid item xs={false} sm={4} md={7} className={classes.image} /> */}
      <div className={classNames(classes.paper, 'w-screen h-screen')}>
        <video
          loop
          muted
          autoPlay
          playsInline
          style={{
            objectFit: 'cover',
            width: '100vw',
            height: '100vh',
            top: 0,
            padding: 'none',
            position: 'fixed',
            filter: 'brightness(40%)',
            zIndex: 0,
          }}
        >
          <source src={backgroundVideo} type="video/mp4" />
        </video>
        <Avatar className={classes.avatar} style={{ zIndex: 2, marginTop: '10%', marginBottom: '20px' }}>
          {/* <ImportContactsIcon style={{ color: 'green', fontSize: '2.5em' }} /> */}
          <img
            alt="chungnam_logo"
            src={logoImg}
            onError={(error) => {
              error.currentTarget.src = noImg;
            }}
            style={{
              width: '100%',
            }}
          />
        </Avatar>
        <Typography
          component="h1"
          variant="h5"
          style={{
            zIndex: 2,
            color: 'white',
            cursor: 'default',
            // fontFamily: 'bmjua',
            fontFamily: 'yun350',
            fontWeight: 'bold',
            fontSize: '2.5em',
          }}
        >
          BMS LOGIN
        </Typography>
        <Typography
          component="h4"
          variant="h5"
          style={{
            zIndex: 2,
            color: 'white',
            cursor: 'default',
            fontFamily: 'yun350',
            fontSize: '1.5em',
          }}
        >
          {/* Namdong Nonhyeon Public Library */}
          {`Bulmu Elementary School`}
        </Typography>

        <div
          style={{
            width: '30%',
            minWidth: '400px',
            zIndex: 99,
            marginTop: '20px',
          }}
        >
          <form
            className={classes.form}
            noValidate
            onSubmit={handleSubmit}
            style={{
              zIndex: 2,
              paddingRight: '20px',
              paddingLeft: '20px',
              marginLeft: '-20px',
            }}
          >
            <div style={{ marginTop: '20px' }}>
              <span style={{ marginTop: '3px' }}>
                <UserOutlined
                  style={{
                    fontSize: '2.5em',
                    color: 'white',
                    marginRight: '10px',
                    position: 'relative',
                    top: '7px',
                  }}
                />
              </span>
              <Input
                className="loginInput"
                style={{
                  color: 'white',
                  backgroundColor: '#ffffff22',
                  fontSize: '1.5em',
                  height: '60px',
                  maxWidth: '662px',
                  minWidth: '250px',
                  width: '85%',
                  border: '3px solid white',
                  borderRadius: '30px',
                  textAlign: 'center',
                  fontFamily: 'bmjua',
                }}
                onChange={onChangeId}
                placeholder="아이디"
              />
            </div>
            <div style={{ marginTop: '20px' }}>
              <LockOutlined
                style={{
                  fontSize: '2.5em',
                  color: 'white',
                  marginRight: '10px',
                  position: 'relative',
                  top: pw.length > 0 ? '8px' : '11px',
                }}
              />
              <Input
                style={{
                  color: 'white',
                  backgroundColor: '#ffffff22',
                  fontSize: '1.5em',
                  border: '3px solid white',
                  height: '60px',
                  maxWidth: '662px',
                  minWidth: '250px',
                  width: '85%',
                  borderRadius: '30px',
                  textAlign: 'center',
                  fontFamily: 'bmjua',
                }}
                onChange={onChangePw}
                placeholder="비밀번호"
                type="password"
                className="loginInput"
              />
            </div>
            <div>
              <LockOutlined
                style={{
                  fontSize: '2.5em',
                  color: 'transparent',
                  marginRight: '10px',
                }}
              />
              <Button
                type="submit"
                fullWidth
                variant="contained"
                color="default"
                style={{
                  backgroundColor: '#ffffff !important',
                  height: '50px',
                  borderRadius: '30px',
                  maxWidth: '662px',
                  minWidth: '250px',
                  width: '85%',
                  boxShadow: 'inset -5px -4px 3px #a8a19c',
                  cursor: 'pointer',
                }}
                className={classes.submit}
              >
                <span
                  style={{
                    fontSize: '1.5em',
                    fontWeight: 'bold',
                    fontFamily: 'bmjua',
                    cursor: 'pointer',
                    color: '#444444',
                  }}
                >
                  로그인
                </span>
              </Button>
            </div>

            <Box mt={5}>
              <Copyright />
            </Box>
          </form>
        </div>
      </div>
    </Grid>
  );
}
