import { DeleteOutlined } from '@ant-design/icons';
import { Modal } from 'antd';
import axios from 'axios';
import classNames from 'classnames';
import React, { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import { deleteFile } from '../../slice/BannerSlice';
import { FileInfo } from '../../types/Banner';

type OffsetSize = {
  width: number;
  height: number;
};
export default function BannerFile({ file }: { file: FileInfo }) {
  const dispatch = useDispatch();

  const [offsetSize, setOffsetSize] = useState<OffsetSize>({
    width: 0,
    height: 0,
  });
  useEffect(() => {
    console.log(offsetSize);
  });

  return (
    <div className={classNames('relative flex items-center p-1', 'w-full')}>
      <img
        className='w-12 rounded-sm cursor-pointer hover:border-yellow-500'
        alt='thumbnail'
        src={`${axios.defaults.baseURL}/files/banner/${file.saved_file_name}`}
        onLoad={(e) => {
          console.log(e);
          setOffsetSize({
            width: e.currentTarget.offsetWidth,
            height: e.currentTarget.offsetHeight,
          });
        }}
        onClick={() => {
          Modal.info({
            modalRender: (node) => {
              return node;
            },

            icon: null,
            closeIcon: <></>,
            maskClosable: true,
            closable: true,
            okButtonProps: { type: 'default' },
            okText: '닫기',
            width: offsetSize.width > offsetSize.height ? '60%' : '',
            content: (
              <img
                className={classNames('w-full object-contain mx-auto')}
                alt='modal_img'
                src={`${axios.defaults.baseURL}/files/banner/${file.saved_file_name}`}
              />
            ),
          });
        }}
      />
      <div className='ml-2'>{file.original_file_name}</div>

      <div className={classNames('absolute right-3')}>
        <DeleteOutlined
          className={classNames('cursor-pointer text-red-500 text-lg')}
          onClick={() => {
            dispatch(deleteFile({ saved_file_name: file.saved_file_name }));
          }}
        />
      </div>
    </div>
  );
}
