import { Button, Form, Input } from 'antd';
import axios from 'axios';
import React from 'react';

const ManagementUser = (props) => {
  const onFinish = (values) => {
    axios
      .post(`/user/regist`, values)
      .then((res) => {})
      .catch((err) => {
        console.error(err);
      });
  };

  return (
    <div>
      <Form
        onFinish={onFinish}
        style={{
          width: '500px',
          margin: 'auto',
          marginTop: '150px',
        }}
      >
        <Form.Item
          label='ID'
          name='adminID'
          rules={[
            {
              required: true,
              whitespace: true,
              message: '사용자 ID를 입력해주세요',
            },
          ]}
        >
          <Input autoFocus />
        </Form.Item>
        <Form.Item
          label='PW'
          name='password'
          rules={[
            {
              required: true,
              whitespace: true,
              message: '사용자 password를 입력해주세요',
            },
          ]}
        >
          <Input.Password />
        </Form.Item>
        <Form.Item>
          <Button type='primary' htmlType='submit' block>
            Submit
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
};

export default ManagementUser;
