import { connectRouter } from 'connected-react-router';
import { History } from 'history';
import { combineReducers } from 'redux';
import BannerReducer, { BANNER } from './slice/BannerSlice';
import CaptionSettingsReduecer, { CAPTION } from './slice/CaptionSlice';
import LssSettingsReducer, { LSS_DETAIL } from './slice/LssSlice';
import SmartSearchSettingsReducer, {
  SMART_SEARCH,
} from './slice/SmartSearchSlice';

export default function createRootReducer(history: History) {
  return combineReducers({
    router: connectRouter(history),
    [LSS_DETAIL]: LssSettingsReducer,
    [CAPTION]: CaptionSettingsReduecer,
    [SMART_SEARCH]: SmartSearchSettingsReducer,
    [BANNER]: BannerReducer,
  });
}
