import { Tabs } from 'antd';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useHistory, useRouteMatch } from 'react-router';
import { MatchParams } from '../../types/Common';
import PowerTable from './PowerTable';
import SysTable from './SysTable';

const ManagementEquip = () => {
  const history = useHistory();
  const match = useRouteMatch<MatchParams>();
  const TabPane = Tabs.TabPane;
  const [dataSource, setDataSource] = useState([]);

  useEffect(() => {
    getEquipIndex();
  }, []);

  const getEquipIndex = () => {
    axios
      .get('/equip/index')
      .then(function (response) {
        setDataSource(response.data as any);
        return response.data;
      })
      .catch(function (error) {
        // handle error
        console.log(error);
      })
      .finally(function () {
        // always executed
      });
  };

  const callback = (key: string) => {
    getEquipIndex();
    history.push(`/equip/${key}`);
  };

  return (
    <Tabs onChange={callback} type='card' activeKey={match.params.key} animated>
      <TabPane tab='전원 관리' key='power'>
        <PowerTable dataSource={dataSource} getEquipIndex={getEquipIndex} />
      </TabPane>
      <TabPane tab='장비 현황' key='equipment'>
        <SysTable dataSource={dataSource} />
      </TabPane>
    </Tabs>
  );
};

export default ManagementEquip;
