import { AndroidFilled, WindowsFilled } from '@ant-design/icons';
import { Button, Table } from 'antd';
import { ColumnsType } from 'antd/lib/table';
import React, { useState } from 'react';
import { useHistory } from 'react-router';
import * as Constants from '../Constants';
import MenuGuide from '../MenuGuide';
import './SysTable.css';
import './TableColumns.css';

const columns: ColumnsType<any> = [
  {
    title: '고유번호',
    dataIndex: 'idx',
    key: '0',
    width: 80,
    align: 'center',
  },
  {
    title: '시스템명',
    dataIndex: 'eqpName',
    width: 200,
    key: '2',
    align: 'center',
  },
  {
    title: 'ICT',
    dataIndex: 'eqpType',
    key: '7',
    width: 90,
    align: 'center',
    render: (text, record, index) => {
      if (text === 'DCS-S') {
        return 'DCS-싱글';
      } else if (text === 'DCS-M') {
        return 'DCS-멀티';
      } else if (text === 'MAG') {
        return text;
      } else {
        return text;
      }
    },
  },
  { title: 'CPU', dataIndex: 'eqpCPU', key: '3', width: 120, align: 'center' },
  {
    title: 'MEM',
    dataIndex: 'eqpMEM',
    key: '4',
    width: 60,
    align: 'center',
    render: (text, record, index) => {
      return text + 'GB';
    },
  },
  {
    title: 'OS',
    dataIndex: 'eqpOS',
    key: '5',
    width: 50,
    align: 'center',
    render: (text, record, index) => {
      return text === 'WINDOWS' ? (
        <WindowsFilled style={{ fontSize: '2em', color: '#00bcf2' }} />
      ) : (
        <AndroidFilled style={{ fontSize: '2em', color: '#a4c639' }} />
      );
    },
  },
  {
    title: '시스템 IP',
    dataIndex: 'eqpIP',
    key: '8',
    width: 150,
    align: 'center',
  },
  {
    title: '전원관리 IP',
    dataIndex: 'pwrMgtAddress',
    key: '6',
    width: 150,
    align: 'center',
  },
  {
    title: '사용여부',
    dataIndex: 'useYN',
    key: '9',
    width: 80,
    align: 'center',
  },
];

const SysTable = ({ dataSource }: { dataSource: any[] }) => {
  const history = useHistory();

  const onRowClick = (record: any, rowIndex: any) => {
    return {
      onClick: (event: any) => {
        var inputString = prompt('관리번호를 입력하세요', '');
        if (inputString === Constants.adminPW) {
          history.push({
            pathname: '/equip/form',
            state: {
              record: record,
              mode: 'read',
            },
          });
        }
      }, // click row
      onDoubleClick: (event: any) => {}, // double click row
      onContextMenu: (event: any) => {}, // right button click row
      onMouseEnter: (event: any) => {}, // mouse enter row
      onMouseLeave: (event: any) => {}, // mouse leave row
    };
  };

  return (
    <>
      <MenuGuide
        title={'장비 관리 ▶ 장비 현황'}
        subTitle={'ICT 시스템 등록이 가능합니다.'}
      />
      <Table
        rowKey={(dataSource) => dataSource.idx}
        dataSource={dataSource}
        columns={columns}
        size='small'
        bordered
        pagination={{
          pageSize: 20,
          onChange: (e) => {
            sessionStorage.setItem('currentPage', e.toString());
          },
        }}
        onRow={onRowClick}
      ></Table>
      <div id='regBtnWrap'>
        <div id='regBtn'>
          <Button
            type='primary'
            block
            onClick={(e) => {
              var inputString = prompt('관리번호를 입력하세요', '');
              if (inputString === Constants.adminPW) {
                history.push({
                  pathname: '/equip/form',
                  state: {
                    mode: 'regist',
                  },
                });
              }
            }}
          >
            등 록
          </Button>
          {/* </Link> */}
        </div>
      </div>
    </>
  );
};

export default SysTable;
