import { DatePicker } from 'antd';
import moment, { now } from 'moment';
import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { editBanner, selectBanner } from '../../slice/BannerSlice';

export default function BannerRangePicker() {
  const { RangePicker } = DatePicker;
  const bannerState = useSelector(selectBanner);
  const dispatch = useDispatch();
  const dateFormat = 'YYYY-MM-DD';

  useEffect(() => {
    dispatch(
      editBanner({
        banner_start_date: moment(now()).format(dateFormat),
        banner_end_date: moment(now()).add(1, 'M').format(dateFormat),
      })
    );
  }, []);
  return (
    <div>
      <RangePicker
        size='small'
        value={[
          moment(bannerState.banner?.banner_start_date),
          moment(bannerState.banner?.banner_end_date),
        ]}
        onChange={(values) => {
          if (values)
            dispatch(
              editBanner({
                ...bannerState.banner,
                banner_start_date: values[0]?.format(dateFormat),
                banner_end_date: values[1]?.format(dateFormat),
              })
            );
        }}
      />
    </div>
  );
}
