import classNames from 'classnames';
import React from 'react';

const MenuGuide = ({
  title,
  subTitle,
}: {
  title: string;
  subTitle?: string;
}) => {
  return (
    <div className={classNames('text-gray-800')}>
      <div>
        <span className='font-bold text-lg'>{title}</span>
      </div>
      <div>
        <span>{subTitle}</span>
      </div>
    </div>
  );
};
export default MenuGuide;
