import React from 'react';
import { ConfigProvider, message } from 'antd';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import './App.css';
import SignInSide from './login/SignInSideAsan';
import { AuthProvider } from './context/AuthContext';
import ko_KR from 'antd/es/locale/ko_KR';
import Main from './main/Main';
// import ManagementPollDetail from './main/poll/ManagementPollDetail';
// import ManagementPollComplete from './main/poll/ManagementPollComplete';
import NotFound from './main/NotFound';
import Axios from 'axios';
import { DEV_SERVER } from './main/Constants';

const App = (props: any) => {
  message.config({
    top: 30,
    duration: 2,
    maxCount: 3,
    // rtl: true,
  });

  // Axios.defaults.baseURL = DEV_SERVER;

  return (
    <ConfigProvider locale={ko_KR}>
      <Router>
        <AuthProvider>
          <Switch>
            <Route exact path='/login' component={SignInSide} />
            {/* <Route exact path="/poll/detail/:master_id" component={ManagementPollDetail} />
            <Route exact path="/poll/complete" component={ManagementPollComplete} /> */}
            <Route path='/' component={Main} />
            <Route component={NotFound} />
          </Switch>
        </AuthProvider>
      </Router>
    </ConfigProvider>
  );
};

export default App;
