import { HomeOutlined, LogoutOutlined } from '@ant-design/icons';
import { Divider } from 'antd';
import classNames from 'classnames';
import React, { useContext } from 'react';
import { Link } from 'react-router-dom';
import { AuthContext } from '../context/AuthContext';

const Menu = (props: React.PropsWithChildren<{}>): JSX.Element => (
  <div
    className={classNames(
      'flex font-bmjua cursor-pointer',
      'hover:text-blue-600'
    )}
  >
    {props.children}
  </div>
);

const Navsub = () => {
  const [, setIsAuth] = useContext(AuthContext);

  return (
    <div className='flex justify-evenly items-center'>
      <Link to='/' style={{ fontFamily: 'bmjua', color: '#000000' }}>
        <Menu>
          <HomeOutlined />
          <div className='ml-1'>HOME</div>
        </Menu>
      </Link>
      <Divider type='vertical' className='bg-gray-800' />
      <Link
        to=''
        onClick={(e) => {
          e.preventDefault();
          setIsAuth(false);
          sessionStorage.clear();
        }}
        className={classNames('font-bmjua text-black')}
      >
        <Menu>
          <LogoutOutlined />
          <div className='ml-1'>LOGOUT</div>
        </Menu>
      </Link>
      {/* <Divider type='vertical' className='bg-gray-800' />
      <Link to='/help'>
        <Menu>
          <QuestionCircleOutlined style={{ cursor: 'pointer' }} />
          <div>HELP</div>
        </Menu>
      </Link> */}
    </div>
  );
};

export default Navsub;
