import classNames from 'classnames';
import React from 'react';
import GesIcon from '../images/greeneffect_icon.png';
import { kBodyWidth } from './Constants';

export default function MainFooter() {
  return (
    <div className={classNames('h-full', 'flex items-center')} style={{ width: kBodyWidth }}>
      <a color="white" href="http://greeneffect.co.kr/" className={classNames('w-28')}>
        <img src={GesIcon} alt="gesicon" />
      </a>
      <div className={classNames('text-white ml-10')}>
        BMS ©2020 Created by{' '}
        <a color="white" href="http://greeneffect.co.kr/">
          GreenEffectSolution
        </a>
      </div>
    </div>
  );
}
