import axios from 'axios';
import classNames from 'classnames';
import React, { useContext } from 'react';
import { Redirect } from 'react-router-dom';
import { AuthContext } from '../context/AuthContext';
import { kBodyWidth } from './Constants';
import MainFooter from './MainFooter';
import MainRouter from './MainRouter';
import Navbar from './Navbar';
import Navsub from './Navsub';

const Main = () => {
  const [isAuth] = useContext(AuthContext);

  axios.interceptors.request.use(
    (config) => {
      if (config.headers)
        config.headers['userInfo'] =
          sessionStorage.getItem('userInfo') || 'guest';

      return config;
    },
    (error) => Promise.reject(error)
  );

  if (!isAuth) {
    return <Redirect to='/login' />;
  }

  return (
    <div
      className={classNames('w-full h-screen', 'flex flex-col items-center')}
    >
      <div className={classNames('w-full bg-gray-800')}>
        <div
          className={classNames('mx-auto my-3')}
          style={{ width: kBodyWidth }}
        >
          <Navbar />
        </div>
      </div>
      <div
        className={classNames('flex justify-end mt-2')}
        style={{ width: kBodyWidth }}
      >
        <Navsub />
      </div>
      <div
        className={classNames('w-full flex-1 mx-auto')}
        style={{ width: kBodyWidth }}
      >
        <MainRouter />
      </div>
      <div
        className={classNames('w-full bg-gray-800 flex justify-center')}
        style={{ minHeight: 60 }}
      >
        <MainFooter />
      </div>
    </div>
  );
};

export default Main;
