import { ResponseBodySmartSearch } from './../types/SmartSearch';
import { createAsyncThunk, createSlice, PayloadAction } from '@reduxjs/toolkit';
import { requestSmartSearch } from '../api/SmartSearch';
import { ResponseVo } from '../types/Common';
import { message } from 'antd';
export const SMART_SEARCH = 'smartSearch';

type SliceState = {
  totalCount: number;
  isLoading: boolean;
  books: Array<{
    title?: string;
    imageUrl?: string;
    isbn?: string;
    isbn13?: string;
    call_no?: string;
    mediaType?: 'book' | 'media';
    spc_code?: string;
  }>;
  keyword: string;
};
type PayloadType = {
  [SMART_SEARCH]: SliceState;
};
type SmartSearchType = {
  keyword: string;
  current_page: number;
};
export const fetchSmartSearch = createAsyncThunk(
  `${SMART_SEARCH}/fetchSmartSearch`,
  async ({ keyword, current_page }: SmartSearchType) => {
    // 무언가를 함
    const response = await requestSmartSearch(keyword, current_page);
    if (!!!response) return undefined;
    return response.data;
  }
);

const smartSearchSlice = createSlice({
  name: `${SMART_SEARCH}`,
  initialState: {
    books: [],
    totalCount: 0,
    keyword: '',
    isLoading: false,
  } as SliceState,
  reducers: {
    setKeyword: (
      state,
      action: PayloadAction<{
        keyword: string;
      }>
    ) => {
      state.keyword = action.payload.keyword;
    },
    resetBooks: (state) => {
      state.books = [];
    },
  },
  extraReducers: {
    [fetchSmartSearch.pending.type]: (state) => {
      state.isLoading = true;
    },
    [fetchSmartSearch.fulfilled.type]: (
      state,
      action: PayloadAction<ResponseVo<ResponseBodySmartSearch>>
    ) => {
      if (action.payload === undefined) {
        state.isLoading = false;
        return;
      }
      if (action.payload?.description.toLowerCase().includes('success')) {
        const result = action.payload.response_body;
        state.books = result.bookVoList;
        state.totalCount = result.totalCount;
      } else {
        state.books = [];
        state.totalCount = 0;
      }
      state.isLoading = false;
    },
  },
});

export const { setKeyword, resetBooks } = smartSearchSlice.actions;
export default smartSearchSlice.reducer;
export const selectSmartSearch = (state: PayloadType) => state;
