/* eslint-disable react-hooks/exhaustive-deps */
import classNames from 'classnames';
import { Reorder } from 'framer-motion';
import React, { useCallback } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { editBanner, selectBanner } from '../../slice/BannerSlice';
import { BannerDtoList, FileInfo } from '../../types/Banner';
import BannerFile from './BannerFile';

export default function BannerFilelist() {
  const bannerState = useSelector(selectBanner);
  const dispatch = useDispatch();

  const onReorder = useCallback(
    (newOrder: FileInfo[]) => {
      dispatch(
        editBanner({
          file_info: newOrder,
        } as BannerDtoList)
      );
    },

    [bannerState.banner]
  );

  return (
    <Reorder.Group
      axis='y'
      onReorder={onReorder}
      values={bannerState.banner?.file_info || []}
    >
      {bannerState.banner?.file_info?.map((file) => {
        return (
          <Reorder.Item
            key={file.saved_file_name}
            value={file}
            className={classNames('border m-1 rounded-md bg-white')}
            whileDrag={{
              scale: 1.02,
              boxShadow: '5px 5px 5px rgba(0,0,0,0.3)',
              borderColor: 'grey',
              cursor: 'grab',
              zIndex: 99,
            }}
          >
            <BannerFile file={file} />
          </Reorder.Item>
        );
      })}
    </Reorder.Group>
  );
}
