/* eslint-disable react-hooks/exhaustive-deps */
import { DeleteOutlined, QuestionCircleOutlined } from '@ant-design/icons';
import { message, Popconfirm, Table } from 'antd';
import { ColumnsType } from 'antd/lib/table';
import axios from 'axios';
import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router';
import { v4 as uuid } from 'uuid';
import { deleteBanner } from '../../api/banner.api';
import { fetchBanners, selectBanner, setBannerType } from '../../slice/BannerSlice';
import { BannerDtoList } from '../../types/Banner';
import { noImg } from '../CommonImage';

export default function RecommandTable() {
  const dispatch = useDispatch();
  const bannerState = useSelector(selectBanner);
  const history = useHistory();

  useEffect(() => {
    dispatch(setBannerType({ banner_type: 'RECOMMEND' }));
  }, []);

  useEffect(() => {
    dispatch(fetchBanners({ cur_page: bannerState.banners.currentPage, banner_type: 'RECOMMEND' }));
  }, [bannerState.banners.currentPage]);

  const columns: ColumnsType<any> = [
    {
      title: 'NO',
      dataIndex: 'banner_idx',
      align: 'center',
      width: 1,
      key: uuid(),
    },
    {
      title: '썸네일',
      dataIndex: 'thumnail',
      align: 'center',
      width: 30,
      key: uuid(),
      render: (item, record: BannerDtoList, idx) => {
        if (record.file_info && record.file_info.length > 0) {
          return (
            <img
              src={`${axios.defaults.baseURL}/files/banner/${record.file_info[0].saved_file_name}`}
              alt="thumnail"
              onError={(e) => {
                e.currentTarget.src = noImg;
              }}
            />
          );
        }
      },
    },
    {
      title: '제목',
      dataIndex: 'title',
      align: 'center',
      width: 200,
      key: uuid(),
      render: (item, record: BannerDtoList, idx) => {
        return (
          <div
            className="w-full h-full hover:text-blue-600"
            onClick={() => {
              history.push(`/recommandImage/${record.banner_idx}`);
            }}
          >
            {item}
          </div>
        );
      },
    },
    {
      title: '파일수',
      dataIndex: 'file_count',
      align: 'center',
      width: 20,
      key: uuid(),
    },
    {
      title: '시작일',
      dataIndex: 'banner_start_date',
      align: 'center',
      width: 50,
      key: uuid(),
    },
    {
      title: '마감일',
      dataIndex: 'banner_end_date',
      align: 'center',
      width: 50,
      key: uuid(),
    },
    {
      title: '삭제',
      dataIndex: 'delete',
      width: 10,
      align: 'center',
      key: uuid(),
      render: (text: any, record: { banner_idx: number }, index: any) => {
        return (
          <Popconfirm
            title="삭제하시겠습니까?"
            icon={<QuestionCircleOutlined style={{ color: 'red' }} />}
            onConfirm={async () => {
              const response = await deleteBanner({
                banner_idx: record.banner_idx,
              });
              if (response.data.status_code === 200) {
                message.success(`삭제되었습니다.`);
                dispatch(fetchBanners({ cur_page: bannerState.banners.currentPage, banner_type: 'RECOMMEND' }));
              }
            }}
            onCancel={() => {
              return;
            }}
            okText="예"
            cancelText="아니오"
          >
            <DeleteOutlined
              style={{ fontSize: '1.5em', color: '#c0392b' }}
              onClick={(e) => {
                e.preventDefault();
              }}
            />
          </Popconfirm>
        );
      },
    },
  ];

  return (
    <Table
      rowKey={() => uuid()}
      bordered
      size="small"
      pagination={{
        pageSize: 10,
        current: bannerState.banners.currentPage,
        total: bannerState.banners.totalCount,
        onChange: (page) => {
          dispatch(fetchBanners({ cur_page: page, banner_type: 'RECOMMEND' }));
        },
      }}
      columns={columns}
      dataSource={bannerState.banners.bannerDtoList}
    />
  );
}
