/* eslint-disable react-hooks/exhaustive-deps */
import { Button, Divider, Input, message } from 'antd';
import classNames from 'classnames';
import moment, { now } from 'moment';
import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory, useRouteMatch } from 'react-router';
import { createBanner, modifyBanner } from '../../api/banner.api';
import { editBanner, emptyBanner, fetchBanner, orderBanners, selectBanner } from '../../slice/BannerSlice';
import { BannerDtoList } from '../../types/Banner';
import FormGuideComponent from '../FormGuideComponent';
import MenuGuide from '../MenuGuide';
import BannerDropzone from './BannerDropzone';
import BannerFilelist from './BannerFilelist';
import BannerRangePicker from './BannerRangePicker';

type ValidationStatus = 'NO_TITLE' | 'NO_PERIOD' | 'NO_FILES' | 'VALIDATED';

const validateBanner = (banner?: BannerDtoList): ValidationStatus => {
  if (!banner?.title || banner.title === '') return 'NO_TITLE';

  if (!banner.banner_start_date || !banner.banner_end_date) return 'NO_PERIOD';

  if (!banner.file_info || banner.file_info.length === 0) return 'NO_FILES';

  return 'VALIDATED';
};

export default function BannerForm() {
  const match = useRouteMatch<{ banner_idx: string | undefined }>();
  const dispatch = useDispatch();
  const history = useHistory();
  const bannerState = useSelector(selectBanner);

  const submit = async () => {
    const banner: BannerDtoList = {
      ...bannerState.banner,
      file_info: orderBanners(bannerState.banner?.file_info),
    };
    console.log(banner);
    if (match.params.banner_idx === 'new') {
      await createBanner({ banner });
    } else {
      await modifyBanner({ banner });
    }
  };

  const dateFormat = 'YYYY-MM-DD';

  useEffect(() => {
    if (match.params.banner_idx === 'new') {
      dispatch(emptyBanner());
      dispatch(
        editBanner({
          banner_start_date: moment(now()).format(dateFormat),
          banner_end_date: moment(now()).add(1, 'M').format(dateFormat),
        })
      );
      return;
    }

    if (match.params.banner_idx) {
      dispatch(fetchBanner({ banner_idx: match.params.banner_idx }));
    }
  }, []);

  return (
    <div className={classNames('w-1/2 h-full mx-auto')}>
      <MenuGuide title="행사 안내 등록" subTitle="이미지를 등록/수정 합니다." />
      <div className={classNames('border p-3')}>
        <div>
          <FormGuideComponent title="제목" />
          <Input
            size="small"
            value={bannerState.banner?.title || ''}
            onChange={(value) => {
              dispatch(
                editBanner({
                  ...bannerState.banner,
                  title: value.currentTarget.value,
                } as BannerDtoList)
              );
            }}
          />
        </div>
        <Divider className="my-2" dashed />
        <div>
          <FormGuideComponent title="게시 기간" />
          <BannerRangePicker />
        </div>
        <Divider className="my-2" dashed />
        <div>
          <FormGuideComponent title="파일" />
          <BannerDropzone />
        </div>
        <Divider className="my-2" dashed />
        <div>
          <FormGuideComponent title="목록" />
          {bannerState.banner?.file_info && <BannerFilelist />}
          {!bannerState.banner?.file_info?.length && <div className="w-full mt-3">업로드된 파일이 없습니다.</div>}
        </div>
        <Divider className="my-2" dashed />
        <Button
          type="primary"
          block
          size="large"
          onClick={() => {
            switch (validateBanner(bannerState.banner)) {
              case 'NO_TITLE':
                message.error('제목을 입력해주세요');
                return;
              case 'NO_PERIOD':
                message.error('게시 기간을 입력해주세요');
                return;
              case 'NO_FILES':
                message.error('이미지를 등록해주세요');
                return;
              default:
                break;
            }

            submit()
              .then(() => {
                message.success('등록되었습니다.');
                if (bannerState.banner?.banner_type === 'EVENT') {
                  history.replace(`/screen/banner`);
                }
                if (bannerState.banner?.banner_type === 'RECOMMEND') {
                  history.replace(`/screen/recommand`);
                }
              })
              .catch((err) => console.log(err));
          }}
        >
          {match.params.banner_idx === 'new' ? '등록' : '수정'}
        </Button>
      </div>
    </div>
  );
}
