import { Progress } from 'antd';
import axios, { AxiosRequestConfig } from 'axios';
import classNames from 'classnames';
import React, { useState } from 'react';
import { DropzoneOptions, useDropzone } from 'react-dropzone';
import { BsCloudUploadFill } from 'react-icons/bs';
import { useDispatch } from 'react-redux';
import { v4 as uuid } from 'uuid';
import { addBannerFiles, addSingleBannerFiles } from '../../slice/BannerSlice';
import { FileInfo } from '../../types/Banner';

export default function BannerDropzone({ options = 'SINGLE' }: { options?: 'MULTI' | 'SINGLE' }) {
  const [progress, setProgress] = useState(0);
  const dispatch = useDispatch();

  const dropzoneOptions: DropzoneOptions = {
    // 행사안내 표현방식 변경에 따른 여러사진 업로드 기능을 삭제함
    multiple: options === 'MULTI',
    onDrop: (acceptedFiles) => {
      const config: AxiosRequestConfig = {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
        onUploadProgress: (progressEvent: { loaded: number; total: number }) => {
          const percentCompleted = Math.round((progressEvent.loaded * 100) / progressEvent.total);
          setProgress(percentCompleted);
        },
      };
      setProgress(0);
      const formData = new FormData();
      const fileInfo: FileInfo[] = [];
      acceptedFiles.forEach((file) => {
        const fileExt = file.name.split('.').pop();
        const renamedFile = new File([file], `${uuid()}.${fileExt}`);
        formData.append('files', renamedFile);
        fileInfo.push({
          original_file_name: file.name,
          saved_file_name: renamedFile.name,
        });
      });

      axios
        .post(`${axios.defaults.baseURL}/file/upload/banner`, formData, config)

        .then((res) => {
          console.log(fileInfo);
          if (options === 'MULTI') {
            dispatch(addBannerFiles([...fileInfo]));
          } else {
            dispatch(addSingleBannerFiles([...fileInfo]));
          }
        });
    },
    accept: 'image/*',
  };

  const { getRootProps, getInputProps, isDragAccept } = useDropzone(dropzoneOptions);

  return (
    <div>
      <section
        {...getRootProps()}
        className={classNames(
          'w-full h-28',
          'outline-none',
          'border border-dashed p-1 rounded-md',
          isDragAccept && 'border-blue-500',

          'bg-gray-100',
          'flex flex-col justify-center items-center',
          'cursor-pointer'
        )}
      >
        <input className="w-full h-full" {...getInputProps()} />

        <BsCloudUploadFill size={200} className="text-gray-400" />
        <p className={classNames('cursor-pointer text-center')}>
          파일을 드래그앤드롭 또는 클릭 후 파일을 선택해 업로드하세요.
          <br />
          이미지 파일만 업로드 가능합니다.
        </p>
      </section>

      {progress > 0 && (
        <Progress
          strokeColor={{
            '0%': '#108ee9',
            '100%': '#87d068',
          }}
          percent={progress}
        />
      )}
    </div>
  );
}
