import React from 'react';
const FormGuideComponent = (props) => {
  const { title, guide, required } = props;
  return (
    <div>
      <div style={{ fontFamily: 'NotoSansKR-Bold', fontSize: '1em' }}>
        {required && <span style={{ color: 'red' }}>*</span>}[{title}]
      </div>
      <div>
        {guide ? (
          <pre
            style={{
              fontFamily: 'NotoSansKR-Medium',
              fontSize: '0.8em',
              padding: '0',
              height: '30px',
              overflow: 'hidden',
              // marginTop: '-20px',
              marginBottom: '0',
              color: '#7f8c8d',
            }}
          >
            {guide}
          </pre>
        ) : (
          <></>
        )}
      </div>
    </div>
  );
};

export default FormGuideComponent;
