import usePreventLeave from '@nooks/use-prevent-leave';
import { Button, Form, Input, InputNumber, message, Radio, Select } from 'antd';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { Prompt } from 'react-router-dom';
import { confirm } from '../ConfirmModal';
import * as Constants from '../Constants';
import FormGuideComponent from '../FormGuideComponent';
import MenuGuide from '../MenuGuide';
import './ManagementEquipForm.css';

const ManagementEquipForm = (props) => {
  const [mode, setMode] = useState(props.location.state.mode);
  const [record, setRecord] = useState(props.location.state.record ? props.location.state.record : '');
  const [os, setOs] = useState(record.eqpOS ? record.eqpOS : '');
  const [useYN, setUseYN] = useState(record.useYN ? record.useYN : '');

  const [isDuplication, setIsDuplication] = useState(true);

  const [isBack, setIsBack] = useState(true);

  const [form] = Form.useForm();

  const { enablePrevent, disablePrevent } = usePreventLeave();

  useEffect(() => {
    document.body.className = 'form-body';
    enablePrevent();

    return () => {
      disablePrevent();
    };
  }, []);

  const validateMessages = {
    required: '필수 항목',
    types: {
      email: 'Not a validate email!',
      number: 'Not a validate number!',
    },
    number: {
      range: 'Must be between ${min} and ${max}',
    },
  };

  const handleSubmit = (values) => {
    var inputString = prompt('관리번호를 입력하세요', '');
    if (inputString === Constants.adminPW) {
      if (mode === 'read') {
        // 읽기 모드일 때 수정 상태로 바꿔주면서 모든 input 상태가 writeable로 변경됨
        setMode('modify');
        return false;
      }

      if (typeof values.eqpCpuCore == 'string') {
        values.eqpCpuCore = values.eqpCpuCore.replace('Core', '');
      }
      if (typeof values.eqpCpuHz == 'string') {
        values.eqpCpuHz = values.eqpCpuHz.replace('GHz', '');
      }

      values.eqpCPU = values.eqpCpuCore + ' ' + values.eqpCpuHz;
      values.eqpID = values.SN;

      if (mode === 'regist') {
        axios
          .post('/equip/regist', values)
          .then(function (response) {
            setIsBack(false);
            message.success({
              content: '장비 등록 완료',
              duration: 3,
              onClose: props.history.push('/equip'),
            });
          })
          .catch(function (error) {
            message.error({
              content: '장비 등록 실패',
              duration: 3,
            });
            console.log(error);
          });
      } else if (mode === 'modify') {
        values.idx = record.idx; // 해당 idx로 수정하기 위해
        axios
          .put('/equip/modify', values)

          .then((res) => {
            setIsBack(false);
            message.success({
              content: '장비 수정 완료',
              duration: 3,
              onClose: props.history.push('/equip'),
            });
          })
          .catch((err) => {
            console.error(err);
          });
      }
    }
  };

  const onDeleteEquipment = (e) => {
    var inputString = prompt('관리번호를 입력하세요', '');
    if (inputString === Constants.adminPW) {
      confirm({
        title: `해당 장비를 삭제합니다.`,
        content: `장비를 삭제하시겠습니까?`,
        onOk: () => {
          axios
            .post('/equip/delete', record)
            .then((res) => {
              // alert('장비가 삭제되었습니다.');
              setIsBack(false);
              props.history.push('/equip');
            })
            .catch((err) => {
              console.error(err);
            });
        },
      });
    }
  };

  useEffect(() => {
    if (mode !== 'regist') {
      form.setFieldsValue({
        SN: record.eqpID,
        eqpName: record.eqpName,
        eqpType: record.eqpType,
        eqpOS: record.eqpOS,
        eqpCpuCore: record.eqpCPU.split(' ')[0].replace('Core', ''),
        eqpCpuHz: record.eqpCPU.split(' ')[1].replace('GHz', ''),
        eqpMEM: record.eqpMEM,
        pwrMgtAddress: record.pwrMgtAddress,
        eqpUniqueID: record.eqpUniqueID,
        eqpPowerModule: record.eqpPowerModule,
        eqpIP: record.eqpIP,
        useYN: record.useYN,
      });
    }

    return () => {};
  }, [props]);

  return (
    <div style={{ width: '50%', margin: 'auto' }}>
      <Prompt when={isBack} message="변경사항이 저장되지 않았습니다. 뒤로가시겠습니까?" />
      <MenuGuide title={'장비 관리 ▶ 장비 등록'} subTitle={'장비 정보를 입력해주세요'} />
      <Form
        // layout='vertical'
        onFinish={handleSubmit}
        validateMessages={validateMessages}
        form={form}
        style={{
          border: '1px solid #dfe6e9',
          padding: '50px',
        }}
      >
        {/* <FormGuideComponent title="S/N" guide="기기의 S/N을 입력해주세요" />
        <Form.Item hasFeedback validateStatus={eqpIDValidStatus}>
          <Form.Item name="SN" noStyle rules={[{ required: true }]}>
            <Input
              placeholder="XXXXXXX-XXXXX-XXXXXXX"
              style={mode === "regist" ? { width: "75%" } : { width: "100%" }}
              readOnly={mode !== "regist" ? true : false}
            />
          </Form.Item>
          <Button
            id="idDuplBtn"
            style={{
              marginLeft: "5px",
              minWidth: "84px",
              position: "relative",
              top: "-2px",
            }}
            onClick={onEqpIdDuplCheck}
            hidden={mode !== "regist" ? true : false}
          >
            중복검사
          </Button>
        </Form.Item> */}

        <FormGuideComponent title="시스템명" />
        <Form.Item name="eqpName" rules={[{ required: true }]}>
          <Input placeholder="Ex) 양방향 미디어 월" className="input_width" readOnly={mode === 'read' ? true : false} />
        </Form.Item>

        <FormGuideComponent title="시스템 타입" />
        <Form.Item name="eqpType" rules={[{ required: true }]}>
          <Select style={{ width: 300 }} placeholder="기기 타입을 선택해주세요" disabled={mode === 'read' ? true : false}>
            <Select.Option value="DCS-S">DCS-싱글</Select.Option>
            <Select.Option value="DCS-M">DCS-멀티</Select.Option>
            <Select.Option value="MAG">MAG</Select.Option>
            <Select.Option value="DMS">DMS</Select.Option>
            <Select.Option value="DPS">DPS</Select.Option>
            <Select.Option value="DLS">DLS</Select.Option>
            <Select.Option value="LIS">LIS</Select.Option>
            <Select.Option value="LSS">LSS</Select.Option>
            <Select.Option value="IS">IS</Select.Option>
            <Select.Option value="LGS">LGS</Select.Option>
            <Select.Option value="LRS">LRS</Select.Option>
            <Select.Option value="ETC-A">안드로이드 OS 기타</Select.Option>
            <Select.Option value="ETC-W">WINDOWS OS 기타</Select.Option>
          </Select>
        </Form.Item>

        <FormGuideComponent title="OS" />
        <Form.Item name="eqpOS" rules={[{ required: true }]}>
          <Radio.Group className="form_radio" disabled={mode === 'read' ? true : false}>
            <Radio value={'WINDOWS'}>WINDOWS</Radio>
            <Radio value={'ANDROID'}>ANDROID</Radio>
          </Radio.Group>
        </Form.Item>
        <FormGuideComponent title="CPU" />
        <div className="input_col_wrap epq_cpu_wrap">
          <Form.Item>
            <Form.Item
              name="eqpCpuCore"
              noStyle
              rules={[
                {
                  required: true,
                },
              ]}
            >
              <InputNumber placeholder="Ex) 4" style={{ width: '60%' }} readOnly={mode === 'read' ? true : false} />
            </Form.Item>
            <span> Core</span>
          </Form.Item>

          <Form.Item>
            <Form.Item
              name="eqpCpuHz"
              noStyle
              rules={[
                {
                  required: true,
                },
              ]}
            >
              <InputNumber placeholder="Ex) 3.6" style={{ width: '60%' }} readOnly={mode === 'read' ? true : false} />
            </Form.Item>
            <span> GHz</span>
          </Form.Item>
        </div>
        <FormGuideComponent title="Memory" />
        <Form.Item>
          <Form.Item name="eqpMEM" noStyle rules={[{ required: true }]}>
            <InputNumber placeholder="Ex) 16" style={{ width: '29.5%' }} readOnly={mode === 'read' ? true : false} />
          </Form.Item>
          <span> GB</span>
        </Form.Item>

        <FormGuideComponent title="전원관리모듈" guide={'SUSPEND 모드 일 때 MAC 주소를 넣으시오.'} />
        <Form.Item name="eqpPowerModule" rules={[{ required: true }]} initialValue={'COSPAN'}>
          <Select disabled={mode === 'read' ? true : false}>
            <Select.Option value={'NEO_1'}>네오테크 1</Select.Option>
            <Select.Option value={'NEO_2'}>네오테크 2</Select.Option>
            <Select.Option value={'COSPAN'}>코스팬</Select.Option>
            <Select.Option value={'SUSPEND'}>절전모드</Select.Option>
          </Select>
        </Form.Item>

        <FormGuideComponent title="전원관리주소" />
        <Form.Item name="pwrMgtAddress" rules={[{ required: true }]}>
          <Input placeholder="Ex)192.168.0.100" className="input_width" readOnly={mode === 'read' ? true : false} />
        </Form.Item>

        <FormGuideComponent title="고유식별ID" />
        <Form.Item name="eqpUniqueID" rules={[{ required: true }]}>
          <Input placeholder="고유 ID" className="input_width" readOnly={mode === 'read' ? true : false} />
        </Form.Item>

        <FormGuideComponent title="IP" />
        <Form.Item name="eqpIP" rules={[{ required: true }]}>
          <Input placeholder="Ex)192.168.0.100" className="input_width" readOnly={mode === 'read' ? true : false} />
        </Form.Item>

        <FormGuideComponent title="사용여부" />
        <Form.Item name="useYN" rules={[{ required: true }]}>
          <Radio.Group className="form_radio" disabled={mode === 'read' ? true : false}>
            <Radio value={'Y'}>예</Radio>
            <Radio value={'N'}>아니오</Radio>
          </Radio.Group>
        </Form.Item>

        <div style={{ width: '100%', textAlign: 'center' }}>
          <Form.Item>
            <Button
              id="regBtn"
              htmlType="submit"
              type="primary"
              size="large"
              style={mode !== 'regist' ? { width: '150px', marginTop: '10px' } : { marginTop: '10px', width: '100%' }}
            >
              {mode === 'regist' ? '등 록' : '수 정'}
            </Button>
            <Button
              id="delBtn"
              type="danger"
              size="large"
              style={{ width: '150px', marginTop: '10px', marginLeft: '5px' }}
              onClick={onDeleteEquipment}
              hidden={mode !== 'read' ? true : false}
            >
              삭 제
            </Button>
          </Form.Item>
        </div>
      </Form>
    </div>
  );
};

export default ManagementEquipForm;
