import Axios from 'axios';
import api from './api';

const API = api.SmartSearch;

export const requestSmartSearch = async (keyword: string, curPageNo = 1, perPage = 10) => {
  try {
    const response = await Axios.get(`${API}`, {
      params: {
        keyword,
        curPageNo,
        perPage,
      },
    });
    return response;
  } catch (err) {
    console.error(err);
    return undefined;
  }
};
