import Axios from 'axios';
import {
  PutReturnDateType,
  PutModeDataType,
  GetReturnDateType,
  EquipIdxType,
  ContentsType,
  PutHolidayType,
  PatchHolidayType,
  PostHolidayType,
} from '../types/Lss';
import apiStrings from './api.server.json';

//mode
export const requestLssGetForReturnDate = async () => {
  try {
    const data: GetReturnDateType = {
      code_id: 'rental_date',
    };
    const response = await Axios.get(`${apiStrings['api-uri'].lss.returnDate}`, {
      params: data,
    });
    return response;
  } catch (error) {
    console.error(error);
  }
};
export const requestLssPutForMode = async (modeData: PutModeDataType) => {
  try {
    const data = modeData;
    const response = await Axios.put(`${apiStrings['api-uri'].lss.mode}`, data);
    return response;
  } catch (error) {
    console.error(error);
  }
};
