/* eslint-disable react-hooks/exhaustive-deps */
import { Button } from 'antd';
import classNames from 'classnames';
import React from 'react';
import { IoMdCreate } from 'react-icons/io';
import { useHistory } from 'react-router';
import MenuGuide from '../MenuGuide';
import BannerTable from './BannerTable';

export default function Banner() {
  const history = useHistory();

  return (
    <div className={classNames('w-full h-full')}>
      <MenuGuide title="화면 관리 ▶ 행사안내(배너)" subTitle="이미지를 등록/수정 합니다." />
      <div className={classNames('w-full flex justify-between items-end', 'my-3')}>
        <Button
          type="primary"
          className="text-white flex items-center"
          onClick={() => {
            history.push('/banner/new');
          }}
        >
          <IoMdCreate />
          <div className="mx-3">등 록</div>
        </Button>
      </div>
      <BannerTable />
    </div>
  );
}
